import View from './View';
import icons from 'url:../../img/icons.svg';

class PaginationView extends View {
  _parentElement = document.querySelector('.pagination');
  _next;
  _prev;

  _calculatePages() {
    const numPages = Math.ceil(this._data.results.length / 10);
    //first page
    if (this._data.page == 1 && numPages > 1) {
      this._next = 2;
      this._prev = undefined;
      return;
    }

    //other pages
    if (this._data.page < numPages && numPages > 1) {
      this._prev = this._data.page - 1;
      this._next = this._data.page + 1;
      return;
    }

    if (this._data.page == numPages && numPages > 1) {
      this._prev = this._data.page - 1;
      this._next = undefined;
      return;
    }

    if ((this._data.page == numPages) == 1 && numPages <= 1) {
      this._prev = undefined;
      this._next = undefined;
      return;
    }
  }
  _generateMarkup() {
    this._calculatePages();
    return `
      
            ${
              this._prev
                ? `
            <button data-goto=${this._prev} class="btn--inline pagination__btn--prev">
            <svg class="search__icon">
            <use href="${icons}#icon-arrow-left"></use>
            </svg>
            <span>Page ${this._prev}</span>
            </button>
            `
                : ``
            }

           
            ${
              this._next
                ? `
            <button data-goto=${this._next} class="btn--inline pagination__btn--next">
            <span>Page ${this._next}</span>
            <svg class="search__icon">
            <use href="${icons}#icon-arrow-right"></use>
            </svg>
            </button>
            `
                : ``
            }

      `;
  }

  addHandlerClick(handler) {
    this._parentElement.addEventListener('click', function (e) {
      e.preventDefault();
      const btn = e.target.closest('.btn--inline');
      if (!btn) return;
      handler(+btn.dataset.goto);
    });
  }
}

export default new PaginationView();
