import 'core-js/stable';
import 'regenerator-runtime/runtime';
import * as Model from './Model.js';
import RecipeView from './Views/recipeView.js';
import searchView from './Views/searchView.js';
import SearchView from './Views/searchView.js';
import resultsView from './Views/resultsView.js';
import paginationView from './Views/paginationView.js';
import BookmarksView from './Views/bookmarksView.js';
import AddRecipeView from './Views/addRecipeView';
import addRecipeView from './Views/addRecipeView';

// https://forkify-api.herokuapp.com/v2

///////////////////////////////////////

const controlRecipe = async function () {
  try {
    const id = window.location.hash.slice(1);

    if (!id) return;

    //0 results view to mark selected seach
    resultsView.update(Model.getSearchResultsPage());
    BookmarksView.update(Model.state.bookmarks);

    //1 loading recipe
    RecipeView.renderSpinner();
    await Model.loadRecipe(id);
    //2 rendering recipe
    const recipe = Model.state.recipe;
    RecipeView.render(recipe);
  } catch (err) {
    RecipeView.renderError();
  }
};

async function controlSearchResults() {
  try {
    resultsView.renderSpinner();
    //1 get query
    const query = searchView.getQuery();

    //2) search query
    await Model.loadSearchResults(query);
    //3) load query
    resultsView.render(Model.getSearchResultsPage());
    paginationView.render(Model.state.search);
  } catch (err) {
    RecipeView.renderError();
  }
}

function controlPagination(page) {
  resultsView.render(Model.getSearchResultsPage(page));
  paginationView.render(Model.state.search);
}

function controlServings(newServing) {
  //update servings
  Model.updateServings(newServing);
  //render servings
  RecipeView.update(Model.state.recipe);
}

function controlAddBookmark() {
  //mark the recipe
  if (!Model.state.recipe.bookmarked) {
    Model.addBookmark(Model.state.recipe);
  } else {
    Model.deleteBookmark(Model.state.recipe.id);
  }

  //render update view
  RecipeView.update(Model.state.recipe);
  BookmarksView.render(Model.state.bookmarks);
}

function controlBookmark() {
  BookmarksView.render(Model.state.bookmarks);
}

async function controlForm(newRecipe) {
  try {
    //load spinner
    AddRecipeView.renderSpinner();

    //upload data to server
    await Model.uploadRecipe(newRecipe);

    //Render the view
    RecipeView.render(Model.state.recipe);

    //Render success message
    AddRecipeView.renderMessage();

    //re-render bookmarks view
    BookmarksView.render(Model.state.bookmarks);

    //update url
    window.history.pushState(null, '', `#${Model.state.recipe.id}`);

    //close widnow
    setTimeout(() => {
      AddRecipeView.toggleWindow();
    }, 2500);
  } catch (err) {
    AddRecipeView.renderError(err.message);
  }
}

function init() {
  BookmarksView.addBookmarkHandler(controlBookmark);
  RecipeView.addHandlerEvent(controlRecipe);
  RecipeView.addHandlerServings(controlServings);
  RecipeView.addHandlerBookmark(controlAddBookmark);
  SearchView.addHandlerSearch(controlSearchResults);
  paginationView.addHandlerClick(controlPagination);
  AddRecipeView.addHandlerUpload(controlForm);
}

init();
