import View from './View';
import icons from 'url:../../img/icons.svg';

class BookmarksView extends View {
  _parentElement = document.querySelector('.bookmarks__list');
  _errorMessage = 'There are no bookmarks yet. Please bookmark one first!';
  _message = '';

  addBookmarkHandler(handler) {
    window.addEventListener('load', function (e) {
      handler();
    });
  }
  _generateMarkup() {
    return this._data.map(this._generateMarkupPreview).join('');
  }
  _generateMarkupPreview(results) {
    const id = window.location.hash.slice(1);
    return ` 
      <li class="preview">
        <a class="preview__link  ${
          results.id == id ? 'preview__link--active' : ''
        }" href="#${results.id}">
          <figure class="preview__fig">
            <img crossOrigin = "anonymous " src="${results.image}" alt="Test" />
          </figure>
          <div class="preview__data">
            <h4 class="preview__title">${results.title}</h4>
            <p class="preview__publisher">${results.publisher}</p>  
            <div class="recipe__user-generated ${results.key ? '' : 'hidden'}">
            <svg>
              <use href="${icons}#icon-user"></use>
            </svg>
          </div>    
          </div>
        </a>
      </li>`;
  }
}

export default new BookmarksView();
